/* http://meyerweb.com/eric/tools/css/reset/ 
     v2.0 | 20110126
     License: none (public domain)
  */
ol, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1.2; }

ol, ul {
  list-style: none; }

textarea, input, select {
  background: transparent; }

template {
  display: block !important; }

html, body {
  margin: 0;
  padding: 0;
  background: #fafafa;
  font-family: Montserrat; }

h1, h2, h3, p {
  margin: 0; }

.cv-container {
  display: flex; }

.cv-sidebar {
  flex-basis: 20%;
  height: 100vh;
  background-color: #436331;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .cv-sidebar .name {
    margin: 20px 0 10px 0; }
  .cv-sidebar .role {
    margin: 0 0 20px 0; }
  .cv-sidebar .contact-info {
    margin-bottom: 40px; }

.cv-contact-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100px;
  justify-content: space-around; }
  .cv-contact-info p {
    margin: 3px 0; }

.cv-content {
  flex-basis: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .cv-content__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px 0; }
    .cv-content__container h3 {
      text-align: center; }
  .cv-content__title {
    color: #436331; }
